
import React from 'react';
import { AppBar, Tabs, Tab } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { Link } from 'react-router-dom';

const AppTopBar = ({ tabs, handleLogout, userStorage }) => {
  const [value, setValue] = React.useState(
    window.sessionStorage.getItem('tab')
  );
  JSON.parse(window.sessionStorage.getItem('isCamera'));
  const useStyles = makeStyles({
    root: {
      background: 'linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)',
      border: 0,
      borderRadius: 3,
      boxShadow: '0 3px 5px 2px rgba(33, 203, 243, .3)',
      color: 'white',
      height: 48,
      padding: '0 30px',
    }
  });
  const classes = useStyles();
  const handleChange = (event, newValue) => {
    window.sessionStorage.setItem('tab', newValue);
    setValue(newValue);
  };

  return (
    <div>
      <AppBar position='sticky' color='primary'>
        <Tabs
          value={value}
          onChange={handleChange}
          className={classes.root}
          variant='scrollable'
          scrollButtons='auto'
        >
          {tabs.map((tab, index) => (
            <Tab
              key={index}
              className={classes.button}
              color='inherit'
              component={Link}
              to={tab}
              label={tab}
              value={tab}
            />
          ))}
          {userStorage ? (
            <Tab
              className={classes.button}
              color='inherit'
              component={Link}
              to='/login'
              onClick={handleLogout}
              label={`logout (${userStorage.username}) `}
              value={'logout'}
            />
          ) : (
            <Tab
              className={classes.button}
              color='inherit'
              component={Link}
              to='/login'
              label={'login'}
              value={'login'}
            />
          )}
        </Tabs>
      </AppBar>
    </div>
  );
};

export default AppTopBar;
