import axios from 'axios';

const getDataFileNames = async (credentials) => {
  const response = await axios.get(
    '/api/modelFitting/get_dataFileNames',
    credentials
  );
  return response.data;
};
const setDataFile = async (credentials) => {
  const response = await axios.post(
    '/api/modelFitting/set_dataFile',
    credentials
  );
  return response.data;
};
const start = async (credentials) => {
  const response = await axios.post(
    '/api/modelFitting/start',
    credentials
  );
  return response.data;
};

export default {
  getDataFileNames,
  setDataFile,
  start,
};
