import React from 'react';
import { Button, Slider, TextField } from '@material-ui/core';

const Drawer = ({
  closeDrawer,
  paraInfo,
  parameters,
  setParameters,
  handleSetParameters,
}) => {
  const handleChangeContinuous = (name) => (event, newValue) => {
    setParameters({
      ...parameters,
      [name]: newValue,
    });
  };
  const style1 = {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  };
  const list = () => (
    <div style={{ marginLeft: '5%', width: '90%' }}>
      <div>
        {paraInfo
          .filter((para) => para.type === 'string')
          .map((para, index) => (
            <div key={index} style={style1}>
              <span>{para.name}:</span>
              <TextField
                helperText={para.description}
                value={parameters[`${para.name}`]}
                onChange={(event) => {
                  setParameters({
                    ...parameters,
                    [para.name]: event.target.value,
                  });
                }}
              />
            </div>
          ))}
      </div>
      <div>
        {paraInfo
          .filter((para) => para.type === 'selection')
          .map((para, index) => (
            <div key={index} style={style1}>
              <span>{para.name}:</span>
              <div>
                <TextField
                  select
                  value={String(parameters[`${para.name}`])}
                  onChange={(event) => {
                    setParameters({
                      ...parameters,
                      [para.name]: event.target.value,
                    });
                  }}
                  SelectProps={{
                    native: true,
                  }}
                >
                  {para.selection.map((option) => (
                    <option key={option.value} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </TextField>
              </div>
            </div>
          ))}
      </div>
      <br />
      <br />
      <div>
        {paraInfo
          .filter((para) => para.type === 'continuous')
          .map((para, index) => (
            <div key={index}>
              <span>{para.name}</span>
              <Slider
                size='large'
                min={para.min}
                max={para.max}
                defaultValue={para.value}
                value={parameters[para.name]}
                name={para.name}
                step={para.step}
                valueLabelDisplay='auto'
                onChange={handleChangeContinuous(para.name)}
                aria-labelledby='Default'
                marks={[
                  { value: para.min, label: para.min },
                  { value: para.max, label: para.max },
                ]}
              />
            </div>
          ))}
      </div>
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'right',
        }}
      >
        <Button
          variant='contained'
          color='primary'
          onClick={closeDrawer}
        >
          close
        </Button>
        <Button
          variant='contained'
          color='secondary'
          onClick={handleSetParameters}
        >
          submit
        </Button>
      </div>
    </div>
  );

  return list();
};

export default Drawer;
