import React, { useState, useEffect } from 'react';
import { Button, TextField } from '@material-ui/core';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import objectManipulationService from '../../services/objectManipulation';
import serviceDispatch from '../../utils/serviceDispatch';
import { useStateValue } from '../../state';
import CodeEditor from '../Command/CodeEditor';
import AlertFrom from '../AmpExp/AlertForm';
import Drawer from '../AmpExp/Drawer';
import io from 'socket.io-client';
const socket = io('http://localhost:5000/manipulation');

const ObjectManipulation = () => {
  const [, dispatch] = useStateValue();
  const [content, setContent] = useState('');
  const [filename, setFileName] = useState('defaultController.py');
  const [settingExpanded1, setSettingExpanded1] = useState(false);
  const [parameters, setParameters] = useState({
    targets: '[(0.2, 0.2)]',
    tunedAmpName: '',
    modelName: '',
    controllerName: '',
    numberOfObj: 1,
    tol: 0.2,
    maxSteps: 10,
  });
  const [paraInfo, setParaInfo] = useState([
    {
      name: 'numberOfObj',
      min: 1,
      max: 10,
      step: 1,
      value: 1,
      type: 'continuous',
      description:
        'How many particles should be on the plate for the experiment to start',
    },
    {
      name: 'tol',
      min: 0.01,
      max: 10,
      step: 0.01,
      value: 0.2,
      type: 'continuous',
      description:
        'The tolerance for the objects manipulation experiment.',
    },
    {
      name: 'maxSteps',
      min: 5,
      max: 100,
      step: 1,
      value: 10,
      type: 'continuous',
      description:
        'The maximum number of the control steps in the object manipulation experiment.',
    },
    {
      name: 'targets',
      value: '[(0.2, 0.2)]',
      type: 'string',
      description:
        'The target array, values are scaled in the range [0,1]',
    },
    {
      name: 'tunedAmpName',
      value: 'please choose file from the list',
      type: 'selection',
      selection: [],
      description:
        'Name of the file storing the tuning amplitude data.',
    },
    {
      name: 'modelName',
      value: 'please choose file from the list',
      type: 'selection',
      selection: [],
      description: 'Name of the file storing the fitted model.',
    },
    {
      name: 'controllerName',
      value: 'please choose file from the list',
      type: 'selection',
      selection: [],
      description:
        'Name of the file storing the controller function.',
    },
  ]);

  const [userStorage, setUserStorage] = useState(
    JSON.parse(window.sessionStorage.getItem('loggedBlogUser'))
  );
  const [openInfo, setOpenInfo] = useState(false);
  const [info, setInfo] = useState('');
  const [openWarning, setOpenWarning] = useState(false);
  const [warning, setWarning] = useState('');
  const [openError, setOpenError] = useState(false);
  const [error, setError] = useState('');

  const handleClose = (event) => {
    setOpenWarning(false);
    socket.emit('startAgain');
  };
  const closeInfoImage = () => {
    const div = document.getElementById(
      'div_info_image_in_manipulation'
    );
    if (div) div.style.display = 'none';
    const image = document.getElementById(
      'info_image_in_manipulation'
    );
    if (image) image.style.display = 'none';
  };
  const handleSetParameters = async (event) => {
    event.preventDefault();

    const service = async () => {
      const res = await objectManipulationService.setParameters({
        user_id: userStorage.id,
        data: { parameters },
      });
      setParameters(res?.data);
      return res;
    };
    serviceDispatch(service, dispatch);
  };

  useEffect(() => {
    socket.on('startPause', function (msg) {
      setOpenWarning(true);
      setWarning(msg);
    });
    socket.on('error', function (msg) {
      setOpenError(true);
      setError(msg);
    });
    socket.on('warning', function (msg) {
      setOpenWarning(true);
      setWarning(msg);
    });
    socket.on('info', function (msg) {
      setOpenInfo(true);
      setInfo(msg);
    });
    socket.on('info_image', function (msg) {
      const div = document.getElementById(
        'div_info_image_in_manipulation'
      );
      if (div) div.style.display = 'block';
      const image = document.getElementById(
        'info_image_in_manipulation'
      );
      image.src = `data:image/jpg;base64,${msg}`;
      image.style.display = 'block';
    });
  }, []);
  useEffect(() => {
    const loggedUser =
      window.sessionStorage.getItem('loggedBlogUser');
    if (loggedUser) {
      setUserStorage(JSON.parse(loggedUser));
    }
    const service = async () => {
      const defaultController =
        await objectManipulationService.getDefaultController();
      setContent(defaultController?.data);
      const files = await objectManipulationService.getControllers();
      const models =
        await objectManipulationService.getModelFileNames();
      const tunedAmpFiles =
        await objectManipulationService.getTunedAmpFileNames();
      setParameters({
        ...parameters,
        tunedAmpName: tunedAmpFiles.data[0],
        modelName: models.data[0],
        controllerName: files.data[0],
      });

      let p = paraInfo;
      p[4].selection = tunedAmpFiles.data.map(function (num) {
        return { value: num, label: num };
      });
      p[5].selection = models.data.map(function (num) {
        return { value: num, label: num };
      });
      p[6].selection = files.data.map(function (num) {
        return { value: num, label: num };
      });
      setParaInfo(p);
    };
    serviceDispatch(service, dispatch);
  }, [dispatch]);

  const handleStart = async (event) => {
    event.preventDefault();
    console.log(parameters);
    const service = async () => {
      const res = await objectManipulationService.start({
        user_id: userStorage.id,
        parameters,
      });
      return res;
    };
    serviceDispatch(service, dispatch);
  };
  const handleSaveController = async (event) => {
    event.preventDefault();
    const service = async () => {
      const res = await objectManipulationService.saveController({
        user_id: userStorage.id,
        filename: filename,
        content: content,
      });
      return res;
    };
    serviceDispatch(service, dispatch);
    const files = await objectManipulationService.getControllers();
    let p = paraInfo;
    p[6].selection = files.data.map(function (num) {
      return { value: num, label: num };
    });
    setParaInfo(p);
  };

  return (
    <div style={{ marginLeft: '5%', width: '90%' }}>
      <AlertFrom
        open={openError}
        handleClose={() => setOpenError(false)}
        severity='error'
        alertText={error}
        buttonText='close'
      />
      <AlertFrom
        open={openWarning}
        handleClose={handleClose}
        severity='warning'
        alertText={warning}
        buttonText='continue'
      />
      <AlertFrom
        open={openInfo}
        handleClose={() => setOpenInfo(false)}
        severity='info'
        alertText={info}
        buttonText='close'
      />
      <Accordion>
        <AccordionSummary aria-controls='panel1a-content'>
          <Typography>Controller--editor</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography component={'span'}>
            <CodeEditor value={content} setValue={setContent} />
            <div
              style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'right',
              }}
            >
              <TextField
                label='file name'
                value={filename}
                onChange={(event) => setFileName(event.target.value)}
              />
              <span>{}</span>
              <Button
                variant='contained'
                color='secondary'
                component='span'
                onClick={handleSaveController}
              >
                save
              </Button>
            </div>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={settingExpanded1}
        onChange={() => setSettingExpanded1((prev) => !prev)}
      >
        <AccordionSummary aria-controls='panel1a-content'>
          <Typography>Setting panel</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography component={'span'}>
            <div>
              <Drawer
                paraInfo={paraInfo}
                parameters={parameters}
                closeDrawer={() => setSettingExpanded1(false)}
                setParameters={setParameters}
                handleSetParameters={handleSetParameters}
              />
            </div>
          </Typography>
        </AccordionDetails>
      </Accordion>

      <Button
        variant='contained'
        color='primary'
        component='span'
        onClick={handleStart}
      >
        {' '}
        start object manipulation
      </Button>
      <div
        id='div_info_image_in_manipulation'
        style={{ display: 'none' }}
      >
        <img
          id='info_image_in_manipulation'
          alt='info_image_in_manipulation'
          style={{ display: 'none', width: '800px', height: '800px' }}
        />
        <IconButton
          aria-label='close'
          color='inherit'
          size='small'
          onClick={closeInfoImage}
        >
          <CloseIcon fontSize='inherit' />
        </IconButton>
      </div>
    </div>
  );
};

export default ObjectManipulation;
