import axios from 'axios';

const playSignal = async (credentials) => {
  const response = await axios.post(
    '/api/command/playSignal',
    credentials
  );
  return response.data;
};
const generateSignal = async (credentials) => {
  const response = await axios.post(
    '/api/command/generateSignal',
    credentials
  );
  return response.data;
};
const command = async (credentials) => {
  const response = await axios.post(
    '/api/command/command',
    credentials
  );
  return response.data;
};
const getCommands = async () => {
  const response = await axios.get(
    '/api/command/getCommands'
  );
  return response.data;
};
export default { playSignal, generateSignal, command, getCommands };
