import axios from 'axios';

const getParameters = async (credentials) => {
  const response = await axios.post(
    '/api/blobDetector/getParameters',
    credentials
  );
  return response.data;
};
const setParameters = async (credentials) => {
  const response = await axios.post(
    '/api/blobDetector/setParameters',
    credentials
  );
  return response.data;
};

const setBlobDetector = async (credentials) => {
  const response = await axios.post(
    '/api/blobDetector/setBlobDetector',
    credentials
  );
  return response.data;
};

const deleteBlobDetector = async (credentials) => {
  const response = await axios.post(
    '/api/blobDetector/deleteBlobDetector',
    credentials
  );
  return response.data;
};

export default {
  getParameters,
  setParameters,
  setBlobDetector,
  deleteBlobDetector,
};
