import axios from 'axios';

const start = async (credentials) => {
  const response = await axios.post('/api/ampExp/start', credentials);
  return response.data;
};
const stop = async (credentials) => {
  const response = await axios.post('/api/ampExp/stop', credentials);
  return response.data;
};
const getParameters = async (credentials) => {
  const response = await axios.post(
    '/api/ampExp/getParameters',
    credentials
  );
  return response.data;
};
const setParameters = async (credentials) => {
  const response = await axios.post(
    '/api/ampExp/setParameters',
    credentials
  );
  return response.data;
};


export default {
  start,
  stop,
  getParameters,
  setParameters,
};
