import React, { useState, useEffect } from 'react';
import {
  Button,
  Slider,
  SwipeableDrawer,
  Divider,
  FormControl,
  FormLabel,
  FormGroup,
  FormControlLabel,
  Checkbox,
} from '@material-ui/core';
import blobDetectorService from '../../services/blobDetector';
import serviceDispatch from '../../utils/serviceDispatch';
import { useStateValue } from '../../state';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
// import imgUrl from '../../images/butterflyBlob.png';
import imgChangedUrl from '../../images/butterflyBlobChanged.png';
import './BlobDetector.css';

const useStyles = makeStyles({
  list: {
    width: 550,
    margin: 20,
  },
  fullList: {
    width: 'auto',
  },
});

const BlobDetector = () => {
  const [, dispatch] = useStateValue();
  const [display, setDisplay] = useState(
    JSON.parse(window.sessionStorage.getItem('isDisplayed')) ?? false
  );
  const classes = useStyles();
  const [popSettingState, setPopSettingState] = useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });
  const [userStorage, setUserStorage] = useState(
    JSON.parse(window.sessionStorage.getItem('loggedBlogUser'))
  );
  const [parameters, setParameters] = useState({
    minThreshold: 10,
    maxThreshold: 1000,
    filterByColor: false,
    blobColor: 200,
    filterByArea: true,
    minArea: 150,
    maxArea: 1500,
    filterByCircularity: false,
    minCircularity: 0.1,
    filterByConvexity: false,
    minConvexity: 0.87,
    filterByInertia: false,
    minInertiaRatio: 0.01,
  });
  const paraInfo = [
    {
      name: 'minThreshold',
      min: 0,
      max: 100,
      step: 1,
      value: 10,
      type: 'continuous',
    },
    {
      name: 'maxThreshold',
      min: 100,
      max: 10000,
      step: 1,
      value: 1000,
      type: 'continuous',
    },
    {
      name: 'blobColor',
      min: 0,
      max: 225,
      step: 1,
      value: 200,
      type: 'continuous',
    },
    {
      name: 'minArea',
      min: 0,
      max: 300,
      step: 1,
      value: 150,
      type: 'continuous',
    },
    {
      name: 'maxArea',
      min: 1000,
      max: 5000,
      step: 1,
      value: 1500,
      type: 'continuous',
    },
    {
      name: 'minCircularity',
      min: 0,
      max: 1,
      step: 0.001,
      value: 0.1,
      type: 'continuous',
    },
    {
      name: 'minConvexity',
      min: 0,
      max: 1,
      step: 0.001,
      value: 0.87,
      type: 'continuous',
    },
    {
      name: 'minInertiaRatio',
      min: 0,
      max: 1,
      step: 0.001,
      value: 0.01,
      type: 'continuous',
    },
    {
      name: 'filterByColor',
      value: false,
      type: 'boolean',
    },
    {
      name: 'filterByArea',
      value: true,
      type: 'boolean',
    },
    {
      name: 'filterByCircularity',
      value: false,
      type: 'boolean',
    },
    {
      name: 'filterByConvexity',
      value: false,
      type: 'boolean',
    },
    {
      name: 'filterByInertia',
      value: false,
      type: 'boolean',
    },
  ];

  useEffect(() => {
    const loggedUser =
      window.sessionStorage.getItem('loggedBlogUser');
    if (loggedUser) {
      setUserStorage(JSON.parse(loggedUser));
    }
  }, []);
  useEffect(() => {
    const service = async () => {
      const res = await blobDetectorService.getParameters({
        user_id: userStorage.id,
      });
      if (res?.data) setParameters(res?.data);
      return res;
    };
    serviceDispatch(service, dispatch);
  }, [userStorage.id, dispatch]);

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setPopSettingState({ ...popSettingState, [anchor]: open });
  };
  const handleChangeContinuous = (name) => (event, newValue) => {
    setParameters({
      ...parameters,
      [name]: newValue,
    });
  };
  const handleChangeBoolean = (name) => (event) => {
    setParameters({
      ...parameters,
      [name]: event.target.checked,
    });
  };
  const handleSetParameters = async (event) => {
    event.preventDefault();
    const service = async () => {
      const res = await blobDetectorService.setParameters({
        user_id: userStorage.id,
        data: parameters,
      });
      return res;
    };
    serviceDispatch(service, dispatch);
  };

  const handleDisplay = async (event) => {
    window.sessionStorage.setItem(
      'isDisplayed',
      JSON.stringify(event.target.checked)
    );
    setDisplay(event.target.checked);
    const service = async () => {
      const res = !display
        ? await blobDetectorService.setBlobDetector({
            user_id: userStorage.id,
          })
        : await blobDetectorService.deleteBlobDetector({
            user_id: userStorage.id,
          });

      const img = document.getElementById('blobDetector');
      img.src = !display ? '/api/camera/video_feed' : imgChangedUrl;
      return res;
    };
    serviceDispatch(service, dispatch);
  };

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      
    >
      <div>
        {paraInfo
          .filter((para) => para.type === 'continuous')
          .map((para, index) => (
            <span key={index}>
              <p>{para.name}</p>
              <Slider
                min={para.min}
                max={para.max}
                defaultValue={para.value}
                value={parameters[para.name]}
                name={para.name}
                step={para.step}
                valueLabelDisplay='on'
                onChange={handleChangeContinuous(para.name)}
                aria-labelledby='continuous-slider-always'
                marks={[
                  { value: para.min, label: para.min },
                  { value: para.max, label: para.max },
                ]}
              />
            </span>
          ))}
      </div>
      <Divider />
      <br />
      <div>
        <FormControl component='fieldset'>
          <FormLabel component='legend'>
            control By which factor
          </FormLabel>
          <FormGroup>
            {paraInfo
              .filter((para) => para.type === 'boolean')
              .map((para, index) => (
                <FormControlLabel
                  key={index}
                  control={
                    <Checkbox
                      checked={parameters[para.name]}
                      onChange={handleChangeBoolean(para.name)}
                    />
                  }
                  label={para.name}
                />
              ))}
          </FormGroup>
        </FormControl>
      </div>
      <Button
        color='primary'
        variant='outlined'
        onClick={toggleDrawer(anchor, false)}
      >
        {' '}
        close
      </Button>
      <Button
        color='primary'
        variant='outlined'
        onClick={(event) => {
          handleSetParameters(event);
        }}
      >
        {' '}
        submit
      </Button>
    </div>
  );

  return (
    <div style={{ marginLeft: '5%', width: '90%' }}>
      <h2>Blob detector display</h2>
      <span>apply blob detector to current image</span>
      <Checkbox
        checked={display}
        label='display'
        onChange={(event) => {
          handleDisplay(event);
        }}
      />
      <br />
      <Button
        color='primary'
        variant='outlined'
        onClick={toggleDrawer('right', true)}
      >
        setting
      </Button>
      <br />
      <img
        id='blobDetector'
        src={display ? '/api/camera/video_feed' : imgChangedUrl}
        alt='changed'
      />
      <div>
        <SwipeableDrawer
          anchor='right'
          open={popSettingState['right']}
          onClose={toggleDrawer('right', false)}
          onOpen={toggleDrawer('right', true)}
        >
          {list('right')}
        </SwipeableDrawer>
      </div>
    </div>
  );
};

export default BlobDetector;
