import React, { useState, useEffect } from 'react';
import {
  Button,
  Switch,
  FormControlLabel,
  ButtonGroup,
  TextField,
} from '@material-ui/core';
import cameraService from '../../services/camera';
import videoImageUrl from '../../images/videoStart.png';
import boxSelection from '../../utils/boxSelection';
import serviceDispatch from '../../utils/serviceDispatch';
import { useStateValue, setCamera } from '../../state';

const Camera = () => {
  const [{ camera }, dispatch] = useStateValue();
  const [disabledStart, setDisabledStart] = useState(false);
  const [selectedVideo, setSelectedVideo] = useState();
  const [videoFiles, setVideoFiles] = useState([]);
  const [cameraStorage, setCameraStorage] = useState(false);
  const [userStorage, setUserStorage] = useState(
    JSON.parse(window.sessionStorage.getItem('loggedBlogUser'))
  );
  const [checked, setChecked] = useState(
    JSON.parse(window.sessionStorage.getItem('isCamera')) ?? false
  );
  const [cropCoordsOLD, setCropCoordsOLD] = useState();
  const [cropCoords, setCropCoords] = useState({
    left: parseFloat(0),
    top: parseFloat(0),
    right: parseFloat(1),
    bottom: parseFloat(1),
  });
  useEffect(() => {
    const service = async () => {
      const res = await cameraService.getCropCords({
        user_id: userStorage.id,
      });
      if (res?.data) {
        const variable = {
          left: res.data.left,
          top: res.data.top,
          right: res.data.right,
          bottom: res.data.bottom,
        };
        setCropCoordsOLD(variable);
      }
      return res;
    };
    serviceDispatch(service, dispatch);
  }, [userStorage.id, dispatch]);
  /**apply boxSelection function to the image object, so that the user can crop the image by select a box area of an image */
  useEffect(() => {
    setTimeout(() => {
      const obj = document.getElementById('video_in_auth_page');
      if (obj) boxSelection(obj, setCropCoords);
    }, 1000);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [document.getElementById('video_in_auth_page')]);

  useEffect(() => {
    const isCamera = window.sessionStorage.getItem('isCamera');
    if (isCamera) {
      setCameraStorage(JSON.parse(isCamera));
    }
    const loggedUser =
      window.sessionStorage.getItem('loggedBlogUser');
    if (loggedUser) {
      setUserStorage(JSON.parse(loggedUser));
    }
    if (camera) {
      setTimeout(() => {
        document.getElementById('video_in_auth_page').src =
          'api/camera/video_feed';
      }, 500);
    }
  }, [camera]);

  const preHandler = (e) => {
    e.preventDefault();
    const boxDiv = document.getElementById('boxDiv');
    if (boxDiv) {
      boxDiv.style.display = 'none';
    }
  };

  const handleStartRecord = async (event) => {
    const service = async () => {
      setDisabledStart(true);
      const res = await cameraService.start_record();
      return res;
    };
    preHandler(event);
    serviceDispatch(service, dispatch);
  };

  const handleStopRecord = async (event) => {
    const service = async () => {
      setDisabledStart(false);
      const res = await cameraService.stop_record();
      return res;
    };
    preHandler(event);
    serviceDispatch(service, dispatch);
  };
  const handleLoadVideo = async (event) => {
    const service = async () => {
      const selectDiv = document.getElementById('videoFileSelection');
      selectDiv.style.display = 'block';
      const res = await cameraService.get_files();
      setVideoFiles(res?.data);
      return res;
    };
    preHandler(event);
    serviceDispatch(service, dispatch);
  };

  const handleSelectFile = async (event) => {
    const service = async () => {
      const listItem = document.getElementById('imgFile');
      const newItem = document.createElement('img');
      listItem.parentNode.replaceChild(newItem, listItem);
      newItem.src = videoImageUrl;
      setSelectedVideo(event.target.value);
      await cameraService.video_fromFile({
        filename: event.target.value,
      });
      newItem.id = 'imgFile';
      newItem.style = {
        marginLeft: 'auto',
        marginRight: 'auto',
        width: '800px',
        height: '600px',
      };
      setTimeout(() => {
        newItem.src = '/api/camera/video_feedFromFile';
      }, 300);
      const selectDiv = document.getElementById('videoFileSelection');
      selectDiv.style.display = 'none';
    };
    serviceDispatch(service, dispatch);
    preHandler(event);
  };

  const handleCrop = async (event) => {
    /**connect to the backend to crop the camera */
    const data = {
      left:
        cropCoordsOLD.left +
        cropCoords.left * (cropCoordsOLD.right - cropCoordsOLD.left),
      top:
        cropCoordsOLD.top +
        cropCoords.top * (cropCoordsOLD.bottom - cropCoordsOLD.top),
      right:
        cropCoordsOLD.left +
        cropCoords.right * (cropCoordsOLD.right - cropCoordsOLD.left),
      bottom:
        cropCoordsOLD.top +
        cropCoords.bottom *
          (cropCoordsOLD.bottom - cropCoordsOLD.top),
    };
    setCropCoordsOLD(data);
    const service = async () => {
      const res = await cameraService.crop({
        user_id: userStorage.id,
        coords: data,
      });
      return res;
    };
    preHandler(event);
    serviceDispatch(service, dispatch);
  };

  const handleRevertCrop = async (event) => {
    /**connect to the backend to revert cropping the camera */

    const service = async () => {
      setCropCoordsOLD({
        left: parseFloat(0),
        top: parseFloat(0),
        right: parseFloat(1),
        bottom: parseFloat(1),
      });
      setCropCoords({
        left: parseFloat(0),
        top: parseFloat(0),
        right: parseFloat(1),
        bottom: parseFloat(1),
      });
      const res = await cameraService.crop({
        user_id: userStorage.id,
        coords: {
          left: parseFloat(0.000001),
          top: parseFloat(0.000001),
          right: parseFloat(0.999999),
          bottom: parseFloat(0.999999),
        },
      });
      return res;
    };
    preHandler(event);
    serviceDispatch(service, dispatch);
  };

  const handleCameraStartStop = async (event) => {
    const service = async () => {
      const res = checked
        ? await cameraService.stop({ user_id: userStorage.id })
        : await cameraService.start({ user_id: userStorage.id });
      dispatch(setCamera(!checked));
      window.sessionStorage.setItem(
        'isCamera',
        JSON.stringify(!checked)
      );
      return res;
    };

    setChecked((prev) => !prev);
    preHandler(event);
    await serviceDispatch(service, dispatch);
    const boxDiv = document.getElementById('boxDiv');
    if (boxDiv) {
      boxDiv.style.display = 'none';
    }
    // window.location.reload();
  };

  return (
    <div
      className='camera'
      style={{ marginLeft: '5%', width: '90%' }}
    >
      <FormControlLabel
        control={
          <Switch
            checked={checked}
            onChange={handleCameraStartStop}
            name='camera'
          />
        }
        label='camera'
        labelPlacement='bottom'
      />
      {cameraStorage ? (
        <div>
          <ButtonGroup
            color='primary'
            aria-label='outlined primary button group'
          >
            <Button
              id='cropButton'
              onClick={handleCrop}
              variant='contained'
              color='primary'
              component='span'
            >
              crop
            </Button>
            <Button
              id='cropButton'
              onClick={handleRevertCrop}
              variant='contained'
              color='primary'
              component='span'
            >
              revert crop
            </Button>
            <Button
              id='startRecordButton'
              variant='contained'
              onClick={handleStartRecord}
              color='primary'
              component='span'
              disabled={disabledStart}
            >
              start record
            </Button>
            <Button
              id='stopRecordButton'
              variant='contained'
              onClick={handleStopRecord}
              color='primary'
              component='span'
              disabled={!disabledStart}
            >
              stop record
            </Button>
          </ButtonGroup>
          <br />
          <div
            id='containerDiv'
            style={{
              display: 'inline-block',
              position: 'relative',
              cursor: 'pointer',
            }}
          >
            <img
              id='video_in_auth_page'
              src='api/camera/video_feed'
              alt='video'
              style={{ width: '800px', height: '800px' }}
            />
            <div
              id='boxDiv'
              style={{
                display: 'none',
                position: 'absolute',
                border: '2px solid red',
              }}
            ></div>
          </div>
        </div>
      ) : (
        <div>
          <Button
            variant='contained'
            color='primary'
            component='span'
            onClick={handleLoadVideo}
          >
            load video from file
          </Button>
          <span id='videoFileSelection' style={{ display: 'none' }}>
            <TextField
              select
              value={selectedVideo}
              onChange={handleSelectFile}
              SelectProps={{
                native: true,
              }}
            >
              <option aria-label='None' value='' />
              {videoFiles.map((option, index) => (
                <option key={index} value={option}>
                  {option}
                </option>
              ))}
            </TextField>
          </span>
          <br />
          <div>
            <img
              id='imgFile'
              src={videoImageUrl}
              alt='camera'
              style={{
                marginLeft: 'auto',
                marginRight: 'auto',
                width: '800px',
                height: '800px',
              }}
            />
          </div>
        </div>
      )}
    </div>
  );
};

export default Camera;
