  const [paraInfo, setParaInfo] = useState([]);
  const [, dispatch] = useStateValue();
  const [userStorage, setUserStorage] = useState(
    JSON.parse(window.sessionStorage.getItem('loggedBlogUser'))
  );
  const [settingExpanded1, setSettingExpanded1] = useState(false);
  const [settingExpanded2, setSettingExpanded2] = useState(false);
  const [openInfo, setOpenInfo] = useState(false);
  const [info, setInfo] = useState('');
  const [openWarning, setOpenWarning] = useState(false);
  const [warning, setWarning] = useState('');
  const [openError, setOpenError] = useState(false);
  const [error, setError] = useState('');
  const handleClose = (event) => {
    setOpenWarning(false);
    socket.emit('startAgain');
  };

  useEffect(() => {
    socket.on('startPause', function (msg) {
      setOpenWarning(true);
      setWarning(msg);
    });
    socket.on('error', function (msg) {
      setOpenError(true);
      setError(msg);
    });
    socket.on('warning', function (msg) {
      setOpenWarning(true);
      setWarning(msg);
    });
    socket.on('info', function (msg) {
      setOpenInfo(true);
      setInfo(msg);
    });
    socket.on('info_image', function (msg) {
      console.log('info_image');
      const image = document.getElementById('info_image_in_amp');
      image.src = `data:image/jpg;base64,${msg}`;
      const div = document.getElementById('div_info_image_in_amp');
      div.style.display = 'block';
      image.style.display = 'block';
    });
  }, []);

  useEffect(() => {
    const loggedUser =
      window.sessionStorage.getItem('loggedBlogUser');
    if (loggedUser) {
      setUserStorage(JSON.parse(loggedUser));
    }
  }, []);

  useEffect(() => {
    const service = async () => {
      const res = await ampExpService.getParameters({
        user_id: userStorage.id,
      });
      if (res?.data) {
        if (res?.data?.data) {
          res.data.data.basescale = `[${res.data.data.basescale}]`;
          setParameters(res.data.data);
        }
        if (res?.data?.paraInfo) setParaInfo(res.data.paraInfo);
      }
      return res;
    };
    serviceDispatch(service, dispatch);
  }, [userStorage.id, dispatch]);
  const closeInfoImage = () => {
    const div = document.getElementById('div_info_image_in_amp');
    if (div) div.style.display = 'none';
    const image = document.getElementById('info_image_in_amp');
    if (image) image.style.display = 'none';
  };
  const str2Arr = (command) => {
    console.log(command);
    const reSplit = /\[|\]/;
    const res1 = command.split(reSplit);
    console.log(res1);
    const res2 = res1[1].toString().split(','); //["261.63", " 277.18", " 293.66", " 311.13", " 329.63", " 349.23", " 369.99", " 392.00", " 415.30", " 440.00", " 466.16", " 493.88"]
    const result = res2.map((e) => parseFloat(e));
    return result;
  };

  const handleStart = async (event) => {
    event.preventDefault();
    const service = async () => {
      const res = await ampExpService.start({
        user_id: userStorage.id,
      });
      return res;
    };
    serviceDispatch(service, dispatch);
    setOpenInfo(false);
  };
  const handleStop = async (event) => {
    event.preventDefault();
    const service = async () => {
      const res = await ampExpService.stop({
        user_id: userStorage.id,
      });
      return res;
    };
    serviceDispatch(service, dispatch);
  };
  const handleSetParameters = async (event) => {
    event.preventDefault();

    const service = async () => {
      const res = await ampExpService.setParameters({
        user_id: userStorage.id,
        data: {
          ...parameters,
          basescale: str2Arr(parameters.basescale),
        },
      });
      if (res?.data) {
        res.data.basescale = `[${res.data.basescale}]`;
        setParameters(res?.data);
      }
      return res;
    };
    serviceDispatch(service, dispatch);
  };
  const toggleDisplay = (id) => {
    const selectDiv = document.getElementById(id);
    if (selectDiv.style.display === 'none') {
      selectDiv.style.display = 'block';
    } else {
      selectDiv.style.display = 'none';
    }
  };

