import React from 'react'

const divStyle =  {
  color: 'red',
  background: 'lightgrey',
  fontSize: '20px',
  borderStyle: 'solid',
  borderRadius: '5px',
  padding: '10px',
  marginBottom: '10px'
}

const ErrorNotification = ({ errorMessage }) => {
  if (errorMessage === null) {
    return null
  }
  return <div style={divStyle} className='errorNotification'>{errorMessage}</div>
}

export default ErrorNotification
