import React, { useState, useEffect } from 'react';
import { Button } from '@material-ui/core';
import Alert from '@mui/material/Alert';
import IconButton from '@mui/material/IconButton';
import Collapse from '@mui/material/Collapse';
import CloseIcon from '@material-ui/icons/Close';
import commandService from '../../services/command';
import serviceDispatch from '../../utils/serviceDispatch';
import { useStateValue } from '../../state';
import CodeEditor from './CodeEditor';
import './Command.css';
import io from 'socket.io-client';
const socket = io('http://localhost:5000/command');

const Command = () => {
  const [commandStr, setCommandStr] = useState('');
  const [, dispatch] = useStateValue();
  const [commandList, setCommandList] = useState([]);
  const [open, setOpen] = useState(false);
  const [info, setInfo] = useState('');

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      socket.emit('commandInfoClose');
      return;
    }
    setOpen(false);
    socket.emit('commandInfoClose');
  };

  useEffect(() => {
    socket.on('commandInfo', function (msg) {
      setOpen(true);
      setInfo(msg);
    });
  }, []);

  const handleSubmitCommand = async (event) => {
    event.preventDefault();
    const service = async () => {
      let res = null;
      res = await commandService.command({ commandStr });
      return res;
    };
    serviceDispatch(service, dispatch);
  };
  const toggleDisplay = (id) => {
    const selectDiv = document.getElementById(id);
    if (selectDiv.style.display === 'none') {
      selectDiv.style.display = 'block';
    } else {
      selectDiv.style.display = 'none';
    }
  };

  useEffect(() => {
    const service = async () => {
      const res = await commandService.getCommands();
      if (res?.data) setCommandList(res?.data);
      return res;
    };
    serviceDispatch(service, dispatch);
  }, [dispatch]);

  return (
    <div style={{marginLeft: '5%',width: '90%'}}>
      <Collapse in={open}>
        <Alert
          severity='info'
          action={
            <IconButton
              aria-label='close'
              color='inherit'
              size='small'
              onClick={handleClose}
            >
              <CloseIcon fontSize='inherit' />
            </IconButton>
          }
          sx={{ width: '80%' }}
        >
          {info}
        </Alert>
      </Collapse>
      <form>
        <CodeEditor value={commandStr} setValue={setCommandStr} />
        <Button
          onClick={handleSubmitCommand}
          variant='contained'
          color='primary'
          component='span'
        >
          submit
        </Button>
        <Button
          onClick={() => toggleDisplay('commandInfo')}
          variant='contained'
          color='primary'
          component='span'
        >
          command list
        </Button>
      </form>
      <div>
        <ul id={'commandInfo'} style={{ display: 'none' }}>
          <h1>Command List </h1>
          {commandList.map((command, index) => (
            <li
              key={index}
              onClick={() =>
                toggleDisplay(`index_${command.functionName}`)
              }
            >
              <h2>{command.functionName}</h2>
              <div
                id={`index_${command.functionName}`}
                style={{ display: 'none' }}
              >
                <ul>
                  {Object.keys(command).map((item, index) => {
                    return (
                      <li
                        key={index}
                      >{`${item}: ${command[item]}`}</li>
                    );
                  })}
                </ul>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default Command;
