import React, { useState } from 'react';
import loginService from '../../services/login';
import { TextField, Button } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import {useStateValue,setUser} from '../../state';
import serviceDispatch from '../../utils/serviceDispatch';

const SignIn = () => {
  const [, dispatch] = useStateValue();
  const history = useHistory();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleSignIn = async (event) => {
    event.preventDefault();
    setUsername('');
    setPassword('');
    const service = async () => {
      const res = await loginService.login({
        username,
        password,
      });
      window.sessionStorage.setItem(
        'loggedBlogUser',
        JSON.stringify(res.data)
      );
      window.sessionStorage.setItem('tab', 'home');
      dispatch(setUser(res.data));
      history.push('/');
      return res;
    };
    serviceDispatch(service, dispatch);
  };

  return (
    <div>
      <div id='signIn' className='signIn_default'>
        <div>
          <TextField
            label='username'
            style={{ margin: 8 }}
            placeholder=''
            value={username}
            onChange={({ target }) => setUsername(target.value)}
            helperText='Please input your user name'
            fullWidth
            margin='normal'
            InputLabelProps={{
              shrink: true,
            }}
          />
        </div>
        <div>
          <TextField
            label='password'
            style={{ margin: 8 }}
            placeholder=''
            type='password'
            value={password}
            onChange={({ target }) => setPassword(target.value)}
            helperText='Please input your password'
            fullWidth
            margin='normal'
            InputLabelProps={{
              shrink: true,
            }}
          />
        </div>
        <div>
          <Button
            variant='outlined'
            id='signIn-button'
            onClick={handleSignIn}
          >
            confirm
          </Button>
        </div>
      </div>
    </div>
  );
};

export default SignIn;
