import React, { useEffect, useState } from 'react';
import { Switch, Route } from 'react-router-dom';
import Login from './pages/Login';
import Camera from './pages/Camera';
import Home from './pages/Home';
import Command from './pages/Command';
import AmpExp from './pages/AmpExp';
import DataCollecting from './pages/DataCollecting';
import ModelFitting from './pages/ModelFitting';
import ObjectManipulation from './pages/ObjectManipulation';
import BlobDetector from './pages/BlobDetector';
import SubPage from './pages/StudentPage';
import Notification from './components/Notification';
import ErrorNotification from './components/ErrorNotification';
import serviceDispatch from './utils/serviceDispatch';
import loginService from './services/login';
import cameraService from './services/camera';
import AppTopBar from './components/AppTopBar';
import { useStateValue, setUser } from './state';
import './App.css';

const App = () => {
  const [{ user, message, errorMessage }, dispatch] = useStateValue();
  const [userStorage, setUserStorage] = useState(null);

  useEffect(() => {
    const loggedUserJSON =
      window.sessionStorage.getItem('loggedBlogUser');
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON);
      setUserStorage(user);
    }
  }, [user]);

  const handleLogout = function (e) {
    e.preventDefault();
    const service = async () => {
      if (JSON.parse(window.sessionStorage.getItem('isCamera'))) {
        if (userStorage.admin) {
          cameraService.stop({ user_id: userStorage.id });
        }
      }
      dispatch(setUser(null));
      window.sessionStorage.clear();
      setUserStorage(null);
      const res = await loginService.logout(userStorage);
      return res;
    };
    serviceDispatch(service, dispatch);
    return null;
  };
  // window.onbeforeunload = handleLogout;
  // window.onunload = handleLogout;

  const AdminTabs = [
    'home',
    'camera',
    'command',
    'ampExp',
    'dataCollecting',
    'modelFitting',
    'objectManipulation',
    'blobDetector',
  ];

  if (userStorage === null) {
    return (
      <div>
        <div style={{ height: '50px', marginTop: '10px' }}>
          <Notification message={message} />
          <ErrorNotification errorMessage={errorMessage} />
        </div>
        <Login />
      </div>
    );
  }

  return (
    <div>
      {userStorage.admin ? (
        <AppTopBar
          tabs={AdminTabs}
          handleLogout={handleLogout}
          userStorage={userStorage}
        ></AppTopBar> 
      ) : (
        <AppTopBar
          tabs={['home', 'camera']}
          handleLogout={handleLogout}
          userStorage={userStorage}
        ></AppTopBar>
      )}
      <div style={{ height: '50px', marginTop: '10px' }}>
        {/* <Notification message={message} />
        <ErrorNotification errorMessage={errorMessage} /> */}
      </div>
      {userStorage.admin ? (
        <Switch>
          <Route path='/blobDetector'>
            <BlobDetector />
          </Route>
          <Route path='/modelFitting'>
            <ModelFitting />
          </Route>
          <Route path='/objectManipulation'>
            <ObjectManipulation />
          </Route>
          <Route path='/dataCollecting'>
            <DataCollecting />
          </Route>
          <Route path='/ampExp'>
            <AmpExp />
          </Route>
          <Route path='/camera'>
            <Camera />
          </Route>
          <Route path='/command'>
            <Command />
          </Route>
          <Route path='/login'>
            <Login />
          </Route>
          <Route path='/home'>
            <Home />
          </Route>
          <Route path='/'>
            <Home />
          </Route>
        </Switch>
      ) : (
        <Switch>
          <Route path='/camera'>
            <SubPage />
          </Route>
          <Route path='/login'>
            <Login />
          </Route>
          <Route path='/home'>
            <Home />
          </Route>
          <Route path='/'>
            <Home />
          </Route>
        </Switch>
      )}
    </div>
  );
};

export default App;
