import axios from 'axios';

const getModelFileNames = async () => {
  const response = await axios.get(
    '/api/objectManipulation/get_dataFileNames'
  );
  return response.data;
};
const getTunedAmpFileNames = async () => {
  const response = await axios.get(
    '/api/objectManipulation/get_tuned_amp'
  );
  return response.data;
};
const setDataFile = async (credentials) => {
  const response = await axios.post(
    '/api/objectManipulation/set_dataFile',
    credentials
  );
  return response.data;
};
const start = async (credentials) => {
  const response = await axios.post(
    '/api/objectManipulation/start',
    credentials
  );
  return response.data;
};
const getDefaultController = async () => {
  const response = await axios.get(
    '/api/objectManipulation/get_controller_default'
  );
  return response.data;
};

const getControllers = async () => {
  const response = await axios.get(
    '/api/objectManipulation/get_controllers'
  );
  return response.data;
};

const saveController = async (credentials) => {
  const response = await axios.post(
    '/api/objectManipulation/save_controller',
    credentials
  );
  return response.data;
};

const setParameters = async (credentials) => {
  const response = await axios.post(
    '/api/objectManipulation/setParameters',
    credentials
  );
  return response.data;
};

export default {
  getModelFileNames,
  getTunedAmpFileNames,
  setDataFile,
  start,
  getDefaultController,
  getControllers,
  saveController,
  setParameters,
};
