import React, { useState, useEffect } from 'react';
import { Button, TextField } from '@material-ui/core';
import AlertFrom from '../AmpExp/AlertForm';
import modelFittingService from '../../services/modelFitting';
import serviceDispatch from '../../utils/serviceDispatch';
import { useStateValue } from '../../state';
import io from 'socket.io-client';

const socket = io('http://localhost:5000/model');
const ModelFitting = () => {
  const [, dispatch] = useStateValue();
  const [userStorage, setUserStorage] = useState(
    JSON.parse(window.sessionStorage.getItem('loggedBlogUser'))
  );
  
  const [selectedData, setSelectedData] = useState();
  const [dataFiles, setDataFiles] = useState([]);
  const [openInfo, setOpenInfo] = useState(false);
  const [info, setInfo] = useState('');
  const [openError, setOpenError] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    const loggedUser =
      window.sessionStorage.getItem('loggedBlogUser');
    if (loggedUser) {
      setUserStorage(JSON.parse(loggedUser));
    }
  }, []);
  useEffect(() => {
    socket.on('error', function (msg) {
      setOpenError(true);
      setError(msg);
    });
    socket.on('info', function (msg) {
      setOpenInfo(true);
      setInfo(msg);
    });
  }, []);

  const handleLoadData = async (event) => {
    const selectDiv = document.getElementById('dataFileSelection');
    selectDiv.style.display = 'block';
    const service = async () => {
      const res = await modelFittingService.getDataFileNames();
      setDataFiles(res?.data);
      return res;
    };
    serviceDispatch(service, dispatch);
    console.log(dataFiles);
  };

  const handleSelectFile = async (event) => {
    const service = async () => {
      setSelectedData(event.target.value);
      await modelFittingService.setDataFile({
        filename: event.target.value,
      });
    };
    serviceDispatch(service, dispatch);
    const selectDiv = document.getElementById('dataFileSelection');
    selectDiv.style.display = 'none';
  };
  const handleStart = async (event) => {
    event.preventDefault();
    const service = async () => {
      const res = await modelFittingService.start({
        user_id: userStorage.id,
      });
      return res;
    };
    serviceDispatch(service, dispatch);
  };

  return (
    <div style={{ marginLeft: '5%', width: '90%' }}>
      <AlertFrom
        open={openError}
        handleClose={() => setOpenError(false)}
        severity='error'
        alertText={error}
        buttonText='close'
      />
      <AlertFrom
        open={openInfo}
        handleClose={() => setOpenInfo(false)}
        severity='info'
        alertText={info}
        buttonText='close'
      />
      <Button
        variant='contained'
        color='primary'
        component='span'
        onClick={handleStart}
      >
        {' '}
        start model fitting
      </Button>
      <Button
        variant='contained'
        color='primary'
        component='span'
        onClick={handleLoadData}
      >
        choose data file
      </Button>
      <span id='dataFileSelection' style={{ display: 'none' }}>
        <TextField
          select
          value={selectedData}
          onChange={handleSelectFile}
          SelectProps={{
            native: true,
          }}
        >
          <option aria-label='None' value='' />
          {dataFiles.map((option, index) => (
            <option key={index} value={option}>
              {option}
            </option>
          ))}
        </TextField>
      </span>
    </div>
  );
};

export default ModelFitting;
