import { setMessage, setErrorMessage } from '../state';

const serviceDispatch = async (service, dispatch) => {
  try {
    let res = await service();
    dispatch(setMessage(res?.message));
    setTimeout(() => {
      dispatch(setMessage(null));
    }, 3000);
  } catch (error) {
    dispatch(setErrorMessage(error.response?.data?.message));
    setTimeout(() => {
      dispatch(setErrorMessage(null));
    }, 3000);
  }
};

export default serviceDispatch;
