import React from 'react';
import { Button } from '@material-ui/core';
import loginImageUrl from '../../images/login.png';
import SignIn from './SignIn';
import SignUp from './SignUp';
import './Login.css';

const Login = () => {
  const preHandler = () => {
    const loginButtons = document.getElementById('loginButtons');
    const showDisplayButtons = loginButtons.style.display === 'block';
    loginButtons.style.display = showDisplayButtons
      ? 'none'
      : 'block';
    const login_header = document.getElementById('login_header');
    const shownLogin_header = login_header.style.display === 'block';
    login_header.style.display = shownLogin_header ? 'none' : 'block';
  };
  const showSignInform = () => {
    preHandler();
    const element = document.getElementById('signIn');
    element.classList.toggle('signIn');
  };
  const showSignUpform = () => {
    preHandler();
    const element = document.getElementById('signUp');
    element.classList.toggle('signUp');
  };

  return (
    <div className='login'>
      <div
        className='login_header'
        id='login_header'
        style={{ display: 'block' }}
      >
        <img src={loginImageUrl} alt='login' />
        <h1>AcoBot Web Application</h1>
      </div>

      <div id='loginButtons' style={{ display: 'block' }}>
        <Button onClick={showSignInform} id='signInButton'>
          Sign in
        </Button>
        <Button onClick={showSignUpform} id='signUpButton'>
          Sign up
        </Button>
      </div>
      <SignIn />
      <SignUp />
    </div>
  );
};

export default Login;
