import axios from 'axios';

const getDataFileNames = async (credentials) => {
  const response = await axios.get(
    '/api/dataCollecting/get_dataFileNames',
    credentials
  );
  return response.data;
};
const setDataFile = async (credentials) => {
  const response = await axios.post(
    '/api/dataCollecting/set_dataFile',
    credentials
  );
  return response.data;
};
const start = async (credentials) => {
  const response = await axios.post(
    '/api/dataCollecting/start',
    credentials
  );
  return response.data;
};
const setParameters = async (credentials) => {
  const response = await axios.post(
    '/api/dataCollecting/setParameters',
    credentials
  );
  return response.data;
};

export default {
  getDataFileNames,
  setDataFile,
  start,
  setParameters,
};
