import React, { useState, useEffect } from 'react';
import { Button } from '@material-ui/core';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import dataCollectingService from '../../services/dataCollecting';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import serviceDispatch from '../../utils/serviceDispatch';
import Drawer from '../AmpExp/Drawer';
import AlertFrom from '../AmpExp/AlertForm';
import { useStateValue } from '../../state';
import io from 'socket.io-client';
const socket = io('http://localhost:5000/data');
const DataCollecting = () => {
  const [parameters, setParameters] = useState({
    cycles: 5,
    desired_particles: 19,
    duration: 500,
    exps_before_reset: 10,
    filename: 'freqAmpData.csv',
  });
  const [paraInfo, setParaInfo] = useState([
    {
      name: 'desired_particles',
      min: 1,
      max: 100,
      step: 1,
      value: 19,
      type: 'continuous',
      description:
        'How many particles should be on the plate, at least, for the experiment to start',
    },
    {
      name: 'cycles',
      min: 1,
      max: 100,
      step: 1,
      value: 5,
      type: 'continuous',
      description:
        'For each frequency, how many PTV steps is taken in total. The total number of exps cycles * number of frequencies',
    },
    {
      name: 'duration',
      min: 100,
      max: 1000,
      step: 1,
      value: 500,
      type: 'continuous',
      description: 'in milliseconds, constant for all notes.',
    },
    {
      name: 'exps_before_reset',
      min: 5,
      max: 50,
      step: 1,
      value: 10,
      type: 'continuous',
      description:
        'The balls are replaced to good locations every this many cycles',
    },
    {
      name: 'filename',
      value: 'freqAmpData.csv',
      type: 'selection',
      selection: [
        {
          value: 'freqAmpData.csv',
          label: 'freqAmpData.csv',
        },
      ],
      description:
        'Name of the file storing the tuning amplitude data.',
    },
  ] );
  const [settingExpanded1, setSettingExpanded1] = useState(false);
  const [, dispatch] = useStateValue();
  const [userStorage, setUserStorage] = useState(
    JSON.parse(window.sessionStorage.getItem('loggedBlogUser'))
  );
  const [openInfo, setOpenInfo] = useState(false);
  const [info, setInfo] = useState('');
  const [openWarning, setOpenWarning] = useState(false);
  const [warning, setWarning] = useState('');
  const [openError, setOpenError] = useState(false);
  const [error, setError] = useState('');

  
  const handleClose = (event) => {
    setOpenWarning(false);
    socket.emit('startAgain');
  };
  const closeInfoImage = () => {
    const div = document.getElementById('div_info_image_in_data');
    if (div) div.style.display = 'none';
    const image = document.getElementById('info_image_in_data');
    if (image) image.style.display = 'none';
  };

  useEffect(() => {
    socket.on('startPause', function (msg) {
      setOpenWarning(true);
      setWarning(msg);
    });
    socket.on('error', function (msg) {
      setOpenError(true);
      setError(msg);
    });
    socket.on('warning', function (msg) {
      setOpenWarning(true);
      setWarning(msg);
    });
    socket.on('info', function (msg) {
      setOpenInfo(true);
      setInfo(msg);
    });
    socket.on('info_image', function (msg) {
      const div = document.getElementById('div_info_image_in_data');
      if (div) div.style.display = 'block';
      const image = document.getElementById('info_image_in_data');
      image.src = `data:image/jpg;base64,${msg}`;
      image.style.display = 'block';
    });
  }, []);

  useEffect(() => {
    const loggedUser =
      window.sessionStorage.getItem('loggedBlogUser');
    if (loggedUser) {
      setUserStorage(JSON.parse(loggedUser));
    }
  }, []);

  useEffect(() => {
    const service = async () => {
      const res = await dataCollectingService.getDataFileNames();
      if (res?.data) {
        const files = res.data;
        const selection = files.map(function (num) {
          return { value: num, label: num };
        });
        let p = paraInfo;
        p[4].selection = selection;
        setParaInfo(p);
      }
      return res;
    };
    serviceDispatch(service, dispatch);
  }, [paraInfo, dispatch]);

  const handleSetParameters = async (event) => {
    event.preventDefault();

    const service = async () => {
      const res = await dataCollectingService.setParameters({
        user_id: userStorage.id,
        data: { parameters },
      });
      setParameters(res?.data);
      return res;
    };
    serviceDispatch(service, dispatch);
  };

  const handleStart = async (event) => {
    event.preventDefault();
    const service = async () => {
      const res = await dataCollectingService.start({
        user_id: userStorage.id,
      });
      return res;
    };
    serviceDispatch(service, dispatch);
  };

  return (
    <div style={{ marginLeft: '5%', width: '90%' }}>
      <AlertFrom
        open={openError}
        handleClose={() => setOpenError(false)}
        severity='error'
        alertText={error}
        buttonText='close'
      />
      <AlertFrom
        open={openWarning}
        handleClose={handleClose}
        severity='warning'
        alertText={warning}
        buttonText='continue'
      />
      <AlertFrom
        open={openInfo}
        handleClose={() => setOpenInfo(false)}
        severity='info'
        alertText={info}
        buttonText='close'
      />
      <Accordion
        expanded={settingExpanded1}
        onChange={() => setSettingExpanded1((prev) => !prev)}
      >
        <AccordionSummary aria-controls='panel1a-content'>
          <Typography>Setting panel</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography component={'span'}>
            <div>
              <Drawer
                paraInfo={paraInfo}
                parameters={parameters}
                closeDrawer={() => setSettingExpanded1(false)}
                setParameters={setParameters}
                handleSetParameters={handleSetParameters}
              />
            </div>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Button
        variant='contained'
        color='primary'
        component='span'
        onClick={handleStart}
      >
        {' '}
        start data collecting
      </Button>
      <div id='div_info_image_in_data' style={{ display: 'none' }}>
        <img
          id='info_image_in_data'
          alt='info_image_in_data'
          style={{ display: 'none', width: '800px', height: '800px' }}
        />
        <IconButton
          aria-label='close'
          color='inherit'
          size='small'
          onClick={closeInfoImage}
        >
          <CloseIcon fontSize='inherit' />
        </IconButton>
      </div>
    </div>
  );
};

export default DataCollecting;
