const boxSelection = (obj, setCropCoords) => {
  /** boxSelection is used to draw a selected box area over a obj
   *  and set the scaled [0,1] cropCoords by using setCropCoords function
   */
  let cropCoords = {
    left: parseFloat(0),
    top: parseFloat(0),
    right: parseFloat(1),
    bottom: parseFloat(1),
  };
  let x = 0,
    y = 0,
    old_x = 0,
    old_y = 0,
    target = null;
  let showDrag = false;
  if (obj) {
    const parent = document.getElementById('containerDiv');
    const boxDiv = document.getElementById('boxDiv');

    parent.addEventListener('mousedown', function (e) {
      target = e.target.getBoundingClientRect();
      old_x = e.clientX - target.left;
      old_y = e.clientY - target.top;
      showDrag = true;
      e.preventDefault();
    });
    parent.addEventListener('mousemove', function (e) {
      if (showDrag) {
        x = e.clientX - target.left;
        y = e.clientY - target.top;
        const boxLeft = x > old_x ? old_x : x;
        const boxTop = y > old_y ? old_y : y;
        const boxWidth = x > old_x ? x - old_x : old_x - x;
        const boxHeight = y > old_y ? y - old_y : old_y - y;
        boxDiv.style.display = 'block';
        boxDiv.style.left = boxLeft + 'px';
        boxDiv.style.top = boxTop + 'px';
        boxDiv.style.width = boxWidth + 'px';
        boxDiv.style.height = boxHeight + 'px';
      }
      e.preventDefault();
    });
    parent.addEventListener('mouseup', function (e) {
      showDrag = false;
      cropCoords.left = Math.max(
        Math.min(old_x, x) / target.width,
        0
      );
      cropCoords.top = Math.max(
        Math.min(old_y, y) / target.height,
        0
      );
      cropCoords.right = Math.min(
        Math.max(old_x, x) / target.width,
        1
      );
      cropCoords.bottom = Math.min(
        Math.max(old_y, y) / target.height,
        1
      );
      setCropCoords(cropCoords);

      setTimeout(() => {
        boxDiv.style.display = 'none';
      }, 3000);
      e.preventDefault();
    });
  }
};

export default boxSelection;
