import axios from 'axios';

const start = async (credentials) => {
  const response = await axios.post(
    '/api/camera/start_camera',
    credentials
  );
  return response.data;
};
const getCropCords = async (credentials) => {
  const response = await axios.post(
    '/api/camera/get_crop_cords',
    credentials
  );
  return response.data;
};
const stop = async (credentials) => {
  const response = await axios.post(
    '/api/camera/stop_camera',
    credentials
  );
  return response.data;
};
const crop = async (credentials) => {
  const response = await axios.post(
    '/api/camera/crop_camera',
    credentials
  );
  return response.data;
};
const start_record = async () => {
  const response = await axios.post('/api/camera/start_record');
  return response.data;
};
const stop_record = async () => {
  const response = await axios.post('/api/camera/stop_record');
  return response.data;
};
const get_files = async () => {
  const response = await axios.get('/api/camera/get_videoFileNames');
  return response.data;
};
const video_fromFile = async (credentials) => {
  const response = await axios.post(
    '/api/camera/set_videoFile',
    credentials
  );
  return response.data;
};
const video_feedFromFile = () => {
  axios.get('/api/camera/video_feedFromFile');
};
export default {
  start,
  stop,
  crop,
  getCropCords,
  start_record,
  stop_record,
  get_files,
  video_fromFile,
  video_feedFromFile,
};
