import React, { useState } from 'react';
import loginService from '../../services/login';
import { TextField, Button } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import {
  useStateValue,
  setUser,
  setMessage,
  setErrorMessage,
} from '../../state';

const SignUp = () => {
  const [, dispatch] = useStateValue();
  const history = useHistory();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [passwordRepeat, setPasswordRepeat] = useState('');

  const handleSignUp = async (event) => {
    event.preventDefault();
    if (password !== passwordRepeat) {
      setUsername('');
      setPassword('');
      setPasswordRepeat('');
      dispatch(
        setErrorMessage(
          'passwords must be the same, please try again.'
        )
      );
      setTimeout(() => {
        dispatch(setErrorMessage(null));
      }, 3000);
    } else {
      try {
        const res = await loginService.register({
          username,
          password,
        });
        window.sessionStorage.setItem(
          'loggedBlogUser',
          JSON.stringify(res.data)
        );
        window.sessionStorage.setItem('tab', 'home');
        setUsername('');
        setPassword('');
        dispatch(setUser(res.data));
        dispatch(
          setMessage(
            `${res.data.username} have successfully sing up!`
          )
        );
        history.push('/');
        setTimeout(() => {
          dispatch(setMessage(null));
        }, 3000);
      } catch (error) {
        setUsername('');
        setPassword('');
        dispatch(setErrorMessage(error.response?.data?.message));
        setPasswordRepeat('');
        setTimeout(() => {
          dispatch(setErrorMessage(null));
        }, 3000);
      }
    }
  };

  return (
    <div id='signUp' className='signUp_default'>
      <div>
        <TextField
          label='username'
          style={{ margin: 8 }}
          placeholder=''
          value={username}
          onChange={({ target }) => setUsername(target.value)}
          helperText='Please input your user name'
          fullWidth
          margin='normal'
          InputLabelProps={{
            shrink: true,
          }}
        />
      </div>
      <div>
        <TextField
          label='password'
          style={{ margin: 8 }}
          placeholder=''
          type='password'
          value={password}
          onChange={({ target }) => setPassword(target.value)}
          helperText='Please input your password'
          fullWidth
          margin='normal'
          InputLabelProps={{
            shrink: true,
          }}
        />
      </div>
      <div>
        <TextField
          label='password repeat'
          style={{ margin: 8 }}
          placeholder=''
          type='password'
          value={passwordRepeat}
          onChange={({ target }) => setPasswordRepeat(target.value)}
          helperText='Please repeat your password'
          fullWidth
          margin='normal'
          InputLabelProps={{
            shrink: true,
          }}
        />
      </div>
      <div>
        <Button
          variant='outlined'
          id='signIn-button'
          onClick={handleSignUp}
        >
          confirm
        </Button>
      </div>
    </div>
  );
};

export default SignUp;
